.contact__inputs-form{
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 18px 0px 50px 0px;
    gap: 30px;
}
.contact__inputs__sender{
    width: 48.8%;
    display: flex;
    flex-flow: column wrap;
    justify-content:flex-start;
    gap: 50px;
}
.contact__inputs-item{
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    padding: 7.5px 0px;
}
.contact__inputs-item::after, .contact__inputs__text::after{
    content: "";
    display: flex;
    box-sizing: border-box;
    height: 1px;
    width:100%;
    border: 1px solid $grey;
}

.contact__inputs-input{
    width: 87%;
}
.contact__inputs__text{
    width: 48%;
    display: flex;
    flex-flow: column nowrap;
}
.contact__button{
    display: block;
    align-self: center;
     margin: 0 auto 50px auto;
     width: 370px;
     height: 58px; 
}
@media screen and (max-width:1200px){
    .contact__button{
    width: 318px;
    }   
}
@media screen and (max-width:998px){
    .contact__inputs__sender{
        padding-left: 50px;
    }
    .contact__inputs__text{
        padding-left: 50px;
        width: 100%;
    }
}
@media screen and (max-width:770px){
    .contact__inputs__sender{
        width: 100%;
}
}
@media screen and (max-width:580px){
    .contact__inputs__text, .contact__inputs__sender{
        padding-left: 0px;
    }
    .contact__button{
        width: 288px;
        height: 50px;
        font-size: 18px;   
    }

}