.header{
   background: $black; 
   position:sticky;
   top: 0px;
}

.header__wrapper{
    display: flex;
    flex-direction: row ;
    justify-content: space-between;
}
.header__icon{
    
height: 30.8px;
width: 87px;


}
.header__logo{
    width:7.43%;
    position:relative;
left: -46.5px;
}
@media screen and (max-width:1200px){
.header__logo{
left: 115.5px;
}
}  

.header__icon-img{
    height: 74px;
    width: 183px;
}
.header__menu{
    margin: 0;
    padding: 0;
    width:48.7%;
    display:flex;
    flex-flow: row nowrap;
    list-style: none;
 
}
.header__menu-item {
    display: flex;
    margin: 0;
    flex-basis: 20%;
}
.header__menu-link{
   
    padding: 28px 35px;
    text-align: center;
    justify-content: center;

    color: $white;
    text-decoration:none;
    font-family: 'Open Sans',sans-serif;
font-style: normal;
font-weight: 600;
font-size: 14px;
display: flex;
align-items: center;
text-align: center;
text-transform: capitalize;
    }
    
    .header__menu-link:active {
    color: $red;
    text-decoration:none
    }
    
    .header__menu-link:visited {
    color:$white;
    text-decoration:none
    }
    
    .header__menu-link:hover {
    color:$coral;
    text-decoration: none;
    }
    #menu__toggle{
        display: none;
    }
    @media screen and (max-width:998px){
    .header__logo{
      left: 0px;
      }
    }

    @media screen and (max-width:770px){
              .burger-menu{
            display: block;
            width: 80%;  
        }
        
        .menu__btn {
            display: flex; 
            margin: auto 0px 0px auto;
            align-self: flex-end;
            align-items: center;
            justify-content: center;
            width: 55px;
            height: 56px;
            cursor: pointer;
            background: var(--black);
          
          }
          .menu__btn > span,
          .menu__btn > span::before,
          .menu__btn > span::after{
            display: block;
            position: relative;
            width: 40px;
            height: 5px;
            background-color: var(--white);
            transition: 0.3s;
          }
          .menu__btn > span::before {
            content: '';
            position: absolute;
            top: -8px;
          }
          .menu__btn > span::after {
            content: '';
            position: absolute;
            top: 8px;
          }
          .header__menu {
            display: none;
            
          }
          .header__menu-item {
            display: block;
            
          }
          .header__menu-item:hover {
            background-color: var(--black);
          }
          
          #menu__toggle:checked ~ .menu__btn > span {
            transform: rotate(45deg);
          }
          
          #menu__toggle:checked ~ .menu__btn > span::before {
            top: 0;
            transform: rotate(0);
          }
          
          #menu__toggle:checked ~ .menu__btn > span::after {
            top: 0;
            transform: rotate(90deg);
          }
          
          #menu__toggle:checked ~ .header__menu  {
            display: flex;
            flex-flow: column nowrap;
            width: 100%;
            align-self: start;
            left: 0;
          }
}
@media screen and (max-width:580px){
  .header__icon-img{
    height: 50px;
    width: 130px;
  }
  .header__logo{
    left: 20px;
    }
}